@value mediaQuery from '../../common/size.css';
@value makerWhite from '../../common/colors.css';
.maker{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: makerWhite;
}
.container{
  flex: 1;
  display: flex;
 
}

@media screen and (max-width: mediaQuery) {
  .container{
    flex-direction: column;
  }
}